import React, { Component } from 'react';
import { Absolute, Relative, Tooltip, Column, Text, Flex, Box, NavLink } from 'rebass';
import startCase from 'lodash/startCase';
import kebabCase from 'lodash/kebabCase';
import { withTheme } from 'styled-components';
import Section from 'www/components/Section';
import Link from 'www/components/Link';

const SideBar = ({ edges, pathname, hash, theme }) => {
  return (
    <Flex
      wrap
    >
    {edges.map(({ node }) => {
      const pagePath = node.fields.slug;
      return (
      <Box
        w={1}
        mb={2}
        key={pagePath}>
        <NavLink
          w={1}
          is={Link}
          active={pathname + hash === pagePath}
          to={pagePath}
          >
          {pagePath.slice(5) === '/'
            ? 'Introduction'
            : startCase(pagePath.slice(5))}
        </NavLink>
        <nav>
          {node.headings &&
            node.headings.map((h, i) => {
              if (h.depth !== 2) {
                return;
              }
              const hashPath = pagePath + '#' + kebabCase(h.value);
              return (
                <NavLink
                  w={1}
                  pl={4}
                  key={hashPath}
                  is={Link}
                  key={hashPath}
                  to={hashPath}
                  >
                    {h.value}
                </NavLink>
              );
            })}
          </nav>
        </Box>
      );
    })}
    </Flex>
  )
}

export default withTheme(SideBar);



