import React from 'react';
import Link from 'gatsby-link';
import { Flex, Box } from 'rebass';
import Base from 'www/layouts/_base';
import SideBar from 'www/components/SideBar';
import theme from 'www/theme';

const DocsLayout = ({ children, dark, ...props }) => {
  return (
    <Base {...props}>
      <Flex wrap>
        <Box
          width={[ 1, 1/4 ]}
          color={theme.text(dark)}
          bg={theme.bg(dark)}
          >
          <SideBar
            edges={props.data.allMarkdownRemark.edges}
            pathname={props.location.pathname}
            hash={props.location.hash}
            />
        </Box>
        <Box width={
          [
            1,
            3/4
          ]
        }>
          {children()}
        </Box>
      </Flex>
    </Base>
  );
};

export default DocsLayout;

export const DocsLayoutQuery = graphql`
query DocsLayoutQuery {
  repo {
    name
    description
    html_url
    owner {
      login
      html_url
    }
  }
  allMarkdownRemark(
    filter: {
      fields: { slug: { regex: "/docs/" } }
    }
  ) {
    edges {
      node {
        headings {
          value
          depth
        }
        fields {
          slug
        }
      }
    }
  }
}`;



